<template>
    <div id="view-employee">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{ this.name }}</h4></li>
            <li class="collection-item">Department:   {{ this.dipart }}</li>
            <li class="collection-item">Position:   {{ this.position }}</li>
        </ul>
        <router-link to="/">
            <button class="btn gray">Back</button>
        </router-link>
        <button class="btn red" v-on:click="deleteItem">Delete</button>
        <router-link v-bind:to="'/edit/'+this.employee_id" class="btn blue">update</router-link>
    </div>
</template>

<script>
    import fireStore from "../firebase/firebaseinit";
    export default {
        name: "ViewEmployee",
        data(){
            return{
                employee_id: this.$route.params.employee_id,
                name: null,
                dipart: null,
                position: null
            }
        },
        methods: {
            deleteItem: function () {
                if(confirm('are you sure')) {
                    fireStore.collection('employees').where('employee_id','==', this.employee_id).get().then((querySnapshot) => {
                        querySnapshot.forEach(doc => {
                            doc.ref.delete();
                        })
                    });
                    this.$router.push('/')
                }
            }
        },
        mounted() {
            fireStore.collection('employees').where('employee_id','==', this.employee_id).get().then((querySnapshot) => {
                querySnapshot.forEach(doc => {
                    this.name = doc.data().name;
                    this.dipart = doc.data().dipart;
                    this.position = doc.data().position;
                })
            })
        }
    }
</script>

