<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                Employee
            </li>
            <li v-for="(emp,index) in employees" :key="index" class="collection-item">
                <div class="chip">{{emp.dipart}}</div>
                {{emp.employee_id}}:{{emp.name}}

                <router-link v-bind:to="'/'+ emp.employee_id" class="secondary-content">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>

    </div>
</template>

<script>
    import firebaseStore from "../firebase/firebaseinit";
    export default {
        name: "Dashboard",
        data(){
            return{
                employees: []
            }
        },
        created() {
            firebaseStore.collection('employees').get().then((querySnapshot)=> {
                querySnapshot.forEach(doc => {
                    const data = {
                        'id': doc.id,
                        'employee_id': doc.data().employee_id,
                        'name': doc.data().name,
                        'dipart': doc.data().dipart,
                        'position': doc.data().position
                    };
                    this.employees.push(data);
                })
            })
        }
    }
</script>

