<template>
    <div id="edit-employee">
        <h3>edit employee</h3>
        <div class="row">
            <form>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter name" v-model="name"/>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter department" v-model="dipart"/>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter position" v-model="position"/>
                    </div>
                </div>
                <button v-on:click.prevent="updateEmployee()" class="btn green">update employee</button>
            </form>
        </div>
    </div>
</template>

<script>
    import fireStore from "../firebase/firebaseinit";
    export default {
        name: "EditEmployee",
        data(){
            return{
                employee_id: this.$route.params.employee_id,
                name: null,
                dipart: null,
                position: null
            }
        },
        methods: {
            updateEmployee: function () {
                fireStore.collection('employees').where('employee_id','==', this.employee_id).get().then((dataSanpshot) => {
                    dataSanpshot.forEach(doc => {
                        doc.ref.update({
                            employee_id: this.employee_id,
                            name: this.name,
                            dipart: this.dipart,
                            position: this.position
                        })
                    })
                })
                this.$router.push('/');
            }
        }
    }
</script>

