<template>
    <div id="new-employee">
        <h3>new employee</h3>
        <div class="row">
            <form>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter employee id" v-model="employee_id"/>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter name" v-model="name"/>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter department" v-model="dipart"/>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" placeholder="enter position" v-model="position"/>
                    </div>
                </div>
                <button v-on:click.prevent="addEmployee()" class="btn green">Add employee</button>
            </form>
        </div>
    </div>
</template>

<script>
    import fireStore from "../firebase/firebaseinit";
    export default {
        name: "NewEmployee",
        data(){
            return{
                employee_id: '',
                name: '',
                dipart: '',
                position: ''
            }
        },
        methods: {
            addEmployee: function () {
                fireStore.collection('employees').add({
                    employee_id: this.employee_id,
                    name: this.name,
                    dipart: this.dipart,
                    position: this.position
                }).then(doc => {
                    console.log(doc);
                    this.$router.push('/');
                })
            }
        }
    }
</script>

